<template>
	<div>
		<template v-if="loadingAlumni">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<section class="main-grid__row user-profile">
				<div class="user-profile__card">
					<div class="user-profile__primary">
						<div class="user-profile__avatar">
							<img
								v-if="itemData.avatar && itemData.avatar !== 'fake.jpg'"
								:src="itemData.avatar"
								alt="Profile picture">
							<img
								v-else
								src="../../assets/img/user-profile.png" alt="User profile picture">
						</div>
						<p class="user-profile__name">{{ fullName }}</p>
					</div>

					<div class="user-profile__section">
						<h5 class="user-profile__heading">Basic Information</h5>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Email Address:</p>
							<p>{{ itemData.email }}</p>
						</div>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Phone Number:</p>
							<p>{{ itemData.phone }}</p>
						</div>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Status:</p>
							<p>
								<span :class="isActive">
									{{ itemData.active === 1 ? 'Active' : 'Suspended'}}
								</span>
							</p>
						</div>
					</div>
				</div>
				<div class="user-profile__tabs">
					<ul class="nav nav-tabs" id="myTab" role="tablist">
						<li class="nav-item">
							<a class="nav-link active" id="activity-tab" data-toggle="tab" href="#activity" role="tab" aria-controls="activity" aria-selected="true">Edit Profile</a>
						</li>
					</ul>
					<div class="tab-pane fade show active user-profile__tabs__section" id="settings" role="tabpanel" aria-labelledby="settings-tab">
						<div class="settings">
							<form
									method="post"
									@submit.prevent="updateUser">
									<div class="settings__primary">
										<div class="settings__avatar">
											<img
												v-if="itemData.avatar && itemData.avatar !== 'fake.jpg'"
												:src="itemData.avatar"
												alt="Profile picture">
											<img
												v-else
												src="../../assets/img/user-profile.png"
												alt="user avatar">
											<span class="icon icon--edit">
												<label class="avatar__label" for="upload-image">
													<input
														ref="avatar"
														class="avatar__input"
														id="upload-image"
														type="file"
														@change="changeAvatar"
														accept="image/*">
													<svg width="9" height="9" fill="none" xmlns="http://www.w3.org/2000/svg">
													<path fill="none" d="M0 0h9v9H0z"/>
													<path d="M1.125 6.468v1.407h1.406L6.68 3.727 5.272 2.321 1.125 6.468zM7.766 2.64a.373.373 0 0 0 0-.529l-.877-.878a.373.373 0 0 0-.529 0l-.686.687L7.08 3.326l.686-.686z" fill="#fff"/>
													</svg>
												</label>
											</span>
											<div class="avatar__loader">
												<loader v-if="isUploadingAvatar" size="small" />
											</div>
										</div>
										<button
											@click.prevent="toggleUserStatus"
											class="btn"
											:class="isSuspend"
											v-show="!isSelf"
											type="button"
											data-dismiss="modal"
											aria-label="close"
											name="button">
											{{ itemData.active === 1 ?
												'Suspend User' :
												'Activate User'
											}}
										</button>
										<button
											class="btn btn__save"
											type="button"
											name="button"
											@click.prevent="updateUser"
											v-bind="{'disabled': loading}">
											Save
										</button>
									</div>
									<div class="invalid-feedback">
										{{ errors.avatar }}
									</div>
									<div class="settings__field">
										<div class="form-row">
											<div class="form-group col-md-6">
												<div class="settings__value">
													<label for="userFirstname">First Name</label>
													<input
														:class="[{'is-invalid': errors.fname}]"
														@keydown="clearErrors($event.target.name)"
														v-model="itemData.fname"
														name="fname"
														value=""
														type="text"
														class="form-control"
														id="userFirstname">
												</div>
												<div class="invalid-feedback">
													{{ errors.fname }}
												</div>
											</div>
											<div class="form-group col-md-6">
												<div class="settings__value">
													<label for="userLastname">Last Name</label>
													<input
														:class="[{'is-invalid': errors.lname}]"
														@keydown="clearErrors($event.target.name)"
														v-model="itemData.lname"
														value=""
														name="lname"
														type="text"
														class="form-control"
														id="userLastname">
												</div>
												<div class="invalid-feedback">
													{{ errors.lname }}
												</div>
											</div>
										</div>
									</div>
									<div class="form-group">
										<div class="settings__value">
											<label for="userEmail">Email Address</label>
											<input
												:class="[{'is-invalid': errors.email}]"
												@keydown="clearErrors($event.target.name)"
												v-model="itemData.email"
												name="email"
												value=""
												type="email"
												class="form-control"
												id="userEmail"
												placeholder="Email Address">
										</div>
										<div class="invalid-feedback">
											{{ errors.email }}
										</div>
									</div>
									<div class="form-row">
										<div class="form-row">
											<div class="form-group col-md-6">
												<div class="settings__value">
													<label for="userPhone">Phone Number</label>
													<input
														:class="[{'is-invalid': errors.phone}]"
														@keydown="clearErrors($event.target.name)"
														v-model="itemData.phone"
														name="phone"
														value=""
														type="number"
														class="form-control"
														id="userPhone"
														placeholder="Phone Number">
												</div>
												<div class="invalid-feedback">
													{{ errors.phone }}
												</div>
											</div>
											<div class="form-group col-md-6">
												<div class="settings__value">
													<label for="userPassword">Password</label>
													<input
														:class="[{'is-invalid': errors.password}]"
														@keydown="clearErrors($event.target.name)"
														v-model="itemData.password"
														name="password"
														value=""
														type="password"
														class="form-control"
														id="userPassword">
												</div>
												<div class="invalid-feedback">
													{{ errors.password }}
												</div>
											</div>

										</div>
									</div>
								</form>
						</div>
					</div>
				</div>
			</section>
		</template>
	</div>
</template>

<script>
import { getAlumnus } from '../../utils/api/alumni';
import Loader from '../../components/Loader';
import { updateUser, uploadAvatar } from '../../utils/api/users';
import { success, fail } from '../../utils/toast';
import form from '../../mixins/form';
import user from '../../utils/validations/user';

export default {
	name: 'AlumniProfile',
	mixins: [form],
	data() {
		return {
			itemData: {},
			loadingAlumni: true,
			alumniLoaded: false,
			errors: {},
			avatar: '',
			avatarUploaded: false,
			isUploadingAvatar: false,
			loading: null
		};
	},
	created() {
		this.getAlumni();
	},
	methods: {
		getAlumni() {
			getAlumnus(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.itemData = response.data.data.user;
						this.alumniLoaded = true;
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.alumniLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingAlumni = false;
				});
			return false;
		},
		/**
		* Uploads user avatar
		*/
		uploadAvatar(file) {
			this.isUploadingAvatar = true;
			this.avatarUploaded = false;
			uploadAvatar(this.$route.params.id, file)
				.then((response) => {
					if (response.status === 200) {
						this.itemData.avatar = file;
						this.avatarUploaded = true;
						return true;
					}
					this.avatarUploaded = false;
					return false;
				}).catch((error) => {
					this.avatarUploaded = false;
					return error;
				}).finally(() => {
					this.isUploadingAvatar = false;
				});
			return true;
		},
		/**
		* Checks for image format and if image size < 3MB
		*/
		changeAvatar() {
			const img = this.$refs.avatar.files[0];
			const imageSize = 3 * (2 ** 20);
			if (img && img.type.match(/image\/*/)) {
				if (img.size > imageSize) {
					errors.avatar = 'File size greater than 3MB';
					return false;
				}
			}
			const reader = new FileReader();
			reader.onload = (e) => {
				this.avatar = e.target.result;
				this.uploadAvatar(this.avatar);
			};
			reader.readAsDataURL(img);
			return true;
		},
		toggleUserStatus() {
			this.itemData.active = this.itemData.active === 0 ? 1 : 0;
			updateUser(this.itemData)
				.then((response) => {
					if (response.status === 200) {
						const message = this.itemData.active === 1
							? 'Account has been activated'
							: 'Account has been suspended';
						success({
							title: `Success! ${message}`
						});
					}
					this.loading = false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		updateUser() {
			/**
			 * Validate input
			 */
			const result = this.itemData;
			['credentials', 'avatar', 'classes', 'badges', 'admin', 'category', 'bank', 'created_at', 'updated_at'].forEach((key) => {
				delete result[key];
			});
			this.validate(result, user.update);
			if (Object.keys(this.issues).length > 0) {
				this.errors = this.issues;
				return false;
			}

			updateUser(result)
				.then((response) => {
					if (response.status === 200) {
						if (this.selectedCategory) {
							this.itemData.category = this.categories.find((item) => {
								return item.id === this.selectedCategory;
							});
							this.categoryItem.name = this.itemData.category.name;
						}
						success({
							title: 'Success! Account has been updated!'
						});
					}
					this.loading = false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		}
	},
	computed: {
		fullName() {
			return `${this.itemData.fname} ${this.itemData.lname}`;
		},
		isActive() {
			return [
				this.itemData.active === 1
					? 'badge badge-pill badge-success'
					: 'badge badge-pill badge-danger'
			];
		},
		isSuspend() {
			return [
				this.itemData.active === 1
					? 'btn__danger'
					: 'btn__save'
			];
		},
		isSelf() {
			return this.itemData.id === this.$session.get('id');
		}
	},
	components: {
		Loader
	}
};
</script>


