import axios from 'axios';
import { isLoggedIn } from '../auth';

export function getAlumni(limit, offset) {
	if (isLoggedIn()) {
		return axios.get(`/v1/alumni?limit=${limit}&offset=${offset}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

export function getAlumnus(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/alumni/${id}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

export function createAlumni(data) {
	if (isLoggedIn()) {
		return axios.post('/v1/alumni', data);
	}
	
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

export function updateAlumni(id, data) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/alumni/${id}`, data);
	}
	
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}
